@import '../../styles/helpers';

.switch {
	display: inline-block;

	input[type='checkbox'] {
		display: none;

		&:checked + span {
			background: $skyblue;
			&:after {
				left: 100%;
				transform: translateX(-100%);
			}
		}
	}
	span {
		cursor: pointer;
		width: 34px;
		height: 20px;
		display: block;
		border-radius: 10px;
		position: relative;
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			@include box(18px);
			box-shadow: 0 0 1px $bluishgray;
			background: $white;
			border-radius: 10px;
			transition: all 0.2s $easeOutSine;
		}
	}
	&:active span:after {
		width: 22px;
	}
}

.switch-theme {
	width: 30px;
	height: 30px;
	border-radius: 15px;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;

	&-input {
		appearance: none;
		outline: none;
		cursor: pointer;
		@include box(14px);
		box-shadow: inset calc(14px * 0.33) calc(14px * -0.25) 0;
		// IE compatibility
		-webkit-box-shadow: inset 4.62px -3.5px 0;
		box-shadow: inset 4.62px -3.5px 0;
		border-radius: 999px;
		color: hsl(240, 100%, 95%);
		transition: all 500ms;

		&:checked {
			transform: scale(0.75);
			color: hsl(40, 100%, 50%);
			box-shadow: inset 0 0 0 14px, calc(calc(14px * 0.65) * -1) 0 0 calc(14px * -0.4), calc(14px * 0.65) 0 0 calc(14px * -0.4),
				0 calc(calc(14px * 0.65) * -1) 0 calc(14px * -0.4), 0 calc(14px * 0.65) 0 calc(14px * -0.4),
				calc(calc(14px * 0.45) * -1) calc(calc(14px * 0.45) * -1) 0 calc(14px * -0.4), calc(14px * 0.45) calc(14px * 0.45) 0 calc(14px * -0.4),
				calc(calc(14px * 0.45) * -1) calc(14px * 0.45) 0 calc(14px * -0.4), calc(14px * 0.45) calc(calc(14px * 0.45) * -1) 0 calc(14px * -0.4);
			// IE compatibility
			-webkit-box-shadow: inset 0 0 0 14px, -9.1px 0 0 -5.6px, 9.1px 0 0 -5.6px, 0 -9.1px 0 -5.6px, 0 9.1px 0 -5.6px, -6.3px -6.3px 0 -5.6px,
				6.3px 6.3px 0 -5.6px, -6.3px 6.3px 0 -5.6px, 6.3px -6.3px 0 -5.6px;
			box-shadow: inset 0 0 0 14px, -9.1px 0 0 -5.6px, 9.1px 0 0 -5.6px, 0 -9.1px 0 -5.6px, 0 9.1px 0 -5.6px, -6.3px -6.3px 0 -5.6px,
				6.3px 6.3px 0 -5.6px, -6.3px 6.3px 0 -5.6px, 6.3px -6.3px 0 -5.6px;
		}
		&::-ms-check {
			/* IE 8 */
			-ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=0)';
			/* IE 5-7 */
			filter: alpha(opacity=0);
			/* Good browsers :) */
			opacity: 0;
		}
	}
}
