@import '../../styles/helpers';

.section-wrapper {
	border: 1px solid transparent;


	&-rounded {
		border-radius: 4px;
	}

	.section-header {
		height: 45px;
		padding: 0 14px;
		display: flex;
		align-items: center;
		h4 {
			font-size: 16px;
			font-weight: 600;
			flex: 1;
		}
		&-large {
			height: 50px;
		}
		&-action {
			.section-header-right {
				cursor: pointer;
			}
		}
	}
}
