@import '../../styles/helpers';

$zIndex: 999;

.modal {
	display: grid;
	&__wrapper {
		@include box(100%);
		position: fixed;
		left: 0;
		top: 0;
		z-index: $zIndex;
		pointer-events: none;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	&__overlay {
		background: rgba(0,0,0,0.5);
		position: fixed;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		z-index: $zIndex;
	}
	&__overlay, &__container {
		transition: 0.4s cubic-bezier(0.19, 1, 0.22, 1);
	}
	&__container {
		width: 700px;
		pointer-events: all;
		border-radius: 3px;
		box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.1);
		animation: 0.2s antZoomUpIn forwards;
		-webkit-animation: 0.2s antZoomUpIn forwards;
	}
	&-small { width: 470px; }
	&-middle { width: 700px; }
	&-large { width: 80%; }
	&__title {
		height: 50px;
		padding: 0 14px;
		align-items: center;
		grid-template-columns: 1fr auto;
		font-size: 18px;
		font-weight: 600;
		> div {
			justify-content: flex-start;
			align-items: center;
			flex: 1;
		}
		.icon {
			@include box(14px);
			cursor: pointer;
			fill: currentColor;
			&:hover {
				color: $red;
			}
		}
	}
	&__content {
		padding: 6px 0;
		line-height: normal;


	}
	&__buttons {
		height: 68px;
		display: flex;
		padding: 0 14px;
		align-items: center;
		justify-content: flex-end;

		button {
			font-weight: 600;
		}
	}
	.modal-inner {
		padding: 0 14px;
		> h4 {
			font-weight: 600;
			font-size: 16px;
			margin-top: 20px;
			margin-bottom: 10px;
		}
		.opaque {
			opacity: 0.8;
		}
	}
}
