$animation: 0.4s cubic-bezier(0.19, 1, 0.22, 1) forwards;

.loading {
	animation: fade-in $animation;
	-webkit-animation: fade-in $animation;
	transition: 0.4s cubic-bezier(0.19, 1, 0.22, 1);
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,0.8);
	display: flex;
	align-items: center;
	z-index: 99;
	> div {
		pointer-events: none;
	}
	&.closed {
		animation: fade-out $animation;
		-webkit-animation: fade-out $animation;
	}
}

@keyframes fade-in {
	0% { opacity: 0; }
	100% { opacity: 1; }
}
@-webkit-keyframes fade-in {
	0% { opacity: 0; }
	100% { opacity: 1; }
}

@keyframes fade-out {
	0% { opacity: 1; }
	100% { opacity: 0; }
}
@-webkit-keyframes fade-out {
	0% { opacity: 1; }
	100% { opacity: 0; }
}
