@import '../../styles/helpers';

.popover-menu {
	position: relative;

	cursor: default;
	display: inline-flex;
	justify-content: flex-end;

	&-info {
		align-items: center;
		line-height: normal;
		text-align: right;
		h4 {
			font-weight: 600;
		}
		p {
			font-size: 12px;
		}
	}
	&-image {
		@include box(36px);
		border-radius: 20px;
	}
	&-content {
		position: absolute;
		z-index: 999;
		transition: 0.2s $easeOutBack;
		min-height: 50px;
		right: 0;
		top: calc(100% + 10px);
		min-width: 100px;
		opacity: 0;
		pointer-events: none;
		transform: scale(0.9) translateY(-10px);
		box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
		&:after {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			height: 14px;
			top: -14px;
			background: transparent;
			cursor: default;
		}
	}
	&-item {
		height: 45px;
		transition: $transition;
		.anticon {
			font-size: 15px;
		}
		> .flex {
			height: 45px;
			padding: 0 14px;
			align-items: center;
			cursor: pointer;
		}
		&:hover {
			color: $skyblue;
			background: rgba($color: $skyblue, $alpha: 0.1);
		}
	}
	&-active,
	&-active-hover {
		.popover-menu-content {
			opacity: 1;
			transform: scale(1);
			cursor: pointer;
			pointer-events: all;
		}
	}
}

.popover-menu-item {
	height: 45px;
	transition: $transition;
	.anticon {
		font-size: 15px;
	}
	> .flex {
		height: 45px;
		padding: 0 14px;
		align-items: center;
		cursor: pointer;
	}
	&:hover {
		color: $skyblue;
		background: rgba($color: $skyblue, $alpha: 0.1);
	}
}

.ant-popover-arrow {
	display: none !important;
}

.ant-popover {
	border-radius: 3px;
	box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
	padding: 0 !important;

	&.clean-content {
		.ant-popover-inner-content {
			padding: 0;
		}
	}
}

.ant-popover-title {
	font-size: 16px;
	font-weight: 600;
	padding: 14px 14px 4px 14px;
	border: none;
	position: relative;

	.ant-dropdown {
		font-weight: normal;
	}

	> .ant-dropdown-trigger {
		position: absolute;
		cursor: pointer;
		right: 14px;
		top: 20px;
	}

	> i {
		font-size: 18px;
	}
}
