@import '../../styles/helpers';

.upload-field {
	height: 80px;
	display: flex;
	flex-flow: column;
	border-radius: 3px;
	text-align: center;
	justify-content: center;
	align-items: center;
	transition: 0.2s $easeOutSine;
	cursor: pointer;

	input {
		display: none;
	}
	p { margin-top: 10px; }
	span {
		font-size: 18px;
	}
	&:hover, &.active, &.drag-over {
		border-color: $skyblue;
	}
	&:active {
		opacity: 0.9;
		transform: scale(0.99);
	}
}
