@import '../../styles/helpers';

.ant-tag {
	font-family: Open Sans;
	height: 34px;

	border-radius: 3px;
	font-style: normal;
	font-weight: normal;
	font-size: 14px;
	line-height: 19px;

	border: none;
	padding: 6px 10px;
	border-radius: 3px;
	width: max-content;
	background: #424851;
	border: 1px solid #424851;
	color: #ffffff;

	display: flex;
	align-items: center;
	justify-content: center;

	a:first-child:last-child {
		color: #ffffff;
	}
	svg {
		width: 13px;
		height: 13px;
	}

	&.rounded {
		border-radius: 90px !important;
	}

	&-small {
		height: 30px;
	}
	&-middle {
		height: 34px;
		padding: 0 10px;
	}
	&-large {
		height: 40px;
		padding: 0 12px;
	}

	.anticon {
		margin-left: 4px;
	}
}

.ant-tag-checkable-checked {
	background: $skyblue !important;
}

.light-theme {
	.ant-tag {
		background: #e4f0ff;
		border: 1px solid #e4f0ff;
		color: $gray1;
		.anticon > svg {
			color: #424851;
		}
	}
}

// no antd
.rounded-tag {
	font-size: 14px;
	border-radius: 42px;
	transition: 0.2s all $easeOutSine;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 7px;
	font-weight: 600;
	cursor: default;

	&-small {
		height: 30px;
	}
	&-middle {
		height: 34px;
		padding: 0 10px;
	}
	&-large {
		height: 40px;
		padding: 0 12px;
	}

	&-closable {
		font-weight: 400;
		padding-left: 10px;
	}
	.anticon {
		margin-left: 4px;
		transition: 0.2s $easeOutSine;
		cursor: pointer;
		&:hover {
			color: $red;
		}
	}

	&-no-rounded {
		border-radius: 3px;
	}

	&-disabled {
		opacity: 0.5;
		background: red;
	}
}
