@import '../../styles/helpers';

.switch-options {
	display: inline-flex;
	justify-content: center;
	width: auto;
	border-radius: 3px;
	overflow: hidden;
	position: relative;

	&-small { height: 30px;}
	&-middle { height: 34px;}
	&-large { height: 40px;}

	label, span {
		height: 100%;
		position: relative;
		z-index: 1;
	}

	input{
		display: none;
		&:checked + span {
			color: $white;
			opacity: 1;
		}
	}

	span {
		transition: 0.2s all $easeOutSine;
		display: flex;
		min-width: 60px;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		padding: 0 16px;
		padding-bottom: 1px;
		opacity: 0.8;
		font-size: 15px;
	}
	&-active {
		transition: 0.2s $easeOutSine;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
	}
	&-horizontal {
		height: auto;
		flex-direction: column;
		.switch-options-active {
			bottom: auto;
			right: 0;
		}
		span {
			padding: 8px;
			min-width: 32px;
		}
	}
	&-multiple {
		span {
			border-radius: 90px;
		}
		label + label {
			margin-left: 10px;
		}
		label:active span {
			transform: scale(0.99);
		}
	}
}
