@import '../helpers';

[data-tooltip] {
	position: relative;
	overflow: visible;
	&:after,
	&:before {
		content: '';
		transition: 0.2s all $easeOutSine;
		opacity: 0;
		@include transform(translateX(-50%) translateY(-5px));
		pointer-events: none;
		position: absolute;
		z-index: 99;
		left: 50%;
	}
	&:before {
		width: 0;
		height: 0;
		bottom: -5px;
		border-left: 5px solid transparent;
		border-right: 5px solid transparent;
		border-bottom: 5px solid transparent;
	}
	&:after {
		content: attr(data-tooltip);
		@include b-radius(3px);
		font-weight: 600;
		font-size: 14px;
		border: 1px solid transparent;
		height: 32px;
		line-height: 32px;
		text-align: center;
		white-space: nowrap;
		bottom: -37px;
		padding: 0 10px;
	}
	&:hover {
		&:after,
		&:before {
			opacity: 1;
			@include transform(translateX(-50%) translateY(0));
		}
		&:before {
			opacity: 0.6;
		}
	}
	&[data-top] {
		&:after,
		&:before {
			bottom: none;
		}
		&:after {
			top: -37px;
		}
		&:before {
			top: -5px;
			border-left: 5px solid transparent;
			border-right: 5px solid transparent;
			border-bottom: none;
			border-top: 5px solid transparent;
		}
	}
	&.expanded__tooltip {
		&:after {
			width: 150px;
			white-space: normal;
			bottom: -59px;
			height: 54px;
			text-align: left;
		}
	}
}
