@import '../../styles/helpers';

.navigation-menu {
	position: relative;
	min-width: 195px;
	cursor: default;
	display: inline-flex;
	justify-content: flex-end;

	&-info {
		align-items: center;
		line-height: normal;
		text-align: right;
		h4 {
			font-weight: 600;
		}
		p {
			font-size: 12px;
		}
		.navigation-arrow-down {
			line-height: 36px;
		}
	}
	&-image {
		@include box(36px);
		border-radius: 20px;
	}
	&-content {
		position: absolute;
		transition: 0.2s $easeOutBack;
		min-height: 50px;
		right: 0;
		top: calc(100% + 10px);
		min-width: 195px;
		opacity: 0;
		pointer-events: none;
		transform: scale(0.9) translateY(-10px);
		box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
		&:after {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			height: 14px;
			top: -14px;
			background: transparent;
			cursor: default;
		}
	}
	&-item {
		height: 45px;
		transition: $transition;
		.anticon {
			font-size: 15px;
		}
		> .flex {
			height: 45px;
			padding: 0 14px;
			align-items: center;
			cursor: pointer;
		}
		&:hover {
			color: $skyblue;
			background: rgba($color: $skyblue, $alpha: 0.1);
		}
	}

	.navigation-menu-content {
		z-index: 10;
	}

	&.navigation-menu-click .navigation-arrow-down {
		cursor: pointer;
	}

	&.navigation-menu-active {
		&.navigation-menu-hover .navigation-menu-content,
		&.navigation-menu-click .navigation-menu-content {
			opacity: 1;
			transform: scale(1);
			cursor: pointer;
			pointer-events: all;
		}
	}
}
