html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	vertical-align: baseline;
	font-size: 100%;
	font: inherit;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

body {
	font-family: 'Open Sans', sans-serif;
	line-height: 1;
}

ol,
ul {
	list-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

// Default
*,
*::before,
*::after {
	box-sizing: border-box;
}
.wp-size {
	width: 100%;
	height: 100%;
}
.right {
	float: right;
}
.left {
	float: left;
}
.code,
pre {
	font-family: 'Cascadia Code', source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace !important;
}

a,
a:link,
a:hover,
a:focus {
	text-decoration: none;
}
a {
	color: inherit;
}
img {
	border: none;
	outline: none;

	&:hover {
		border: none;
		outline: none;
	}
}

input {
	cursor: auto;
	user-select: text;
}

button {
	cursor: pointer;
}

button,
a,
input,
select,
textarea {
	font-family: 'Open Sans', sans-serif;
	border: none;
	outline: none;
	&:hover,
	&:focus {
		outline: none;
	}
}
.grid {
	display: grid;
	&-column {
		grid-auto-flow: column;
	}
	&-left {
		justify-content: flex-start;
	}
	&-end {
		justify-content: flex-end;
	}
}
.flex {
	display: flex;
	&-1 {
		flex: 1;
	}
	&-2 {
		flex: 2;
	}
	&-left {
		justify-content: flex-start;
	}
	&-end {
		justify-content: flex-end;
	}
	&-center {
		align-items: center;
	}
	&-middle {
		justify-content: center;
		align-items: center;
	}
	&-column {
		flex-flow: column;
	}
	&-space {
		justify-content: space-between;
	}
	&-gap-4 > * {
		margin-left: 4px;
	}
	&-gap-6 > * {
		margin-left: 6px;
	}
	&-gap-10 > * {
		margin-left: 10px;
	}
	&-gap-14 > * {
		margin-left: 14px;
	}
	&-gap-20 > * {
		margin-left: 20px;
	}
	&-gap-4,
	&-gap-6,
	&-gap-10,
	&-gap-14,
	&-gap-20 {
		> *:first-child {
			margin-left: 0;
		}
	}
	&-vgap-4 > * {
		margin-top: 6px;
	}
	&-vgap-6 > * {
		margin-top: 6px;
	}
	&-vgap-10 > * {
		margin-top: 10px;
	}
	&-vgap-14 > * {
		margin-top: 14px;
	}
	&-vgap-20 > * {
		margin-top: 20px;
	}
	&-vgap-4,
	&-vgap-6,
	&-vgap-10,
	&-vgap-14,
	&-vgap-20 {
		> *:first-child {
			margin-top: 0;
		}
	}
}
.gap-10 {
	gap: 10px;
}
.gap-14 {
	gap: 14px;
}
.w100 {
	width: 100%;
}
.t200 {
	transition: all 0.2s cubic-bezier(0.39, 0.575, 0.565, 1);
}
.t300 {
	transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
}
.h100 {
	height: 100%;
}
.pd-14 {
	padding: 14px;
}
.pdh-14 {
	padding: 0 14px;
}
.pdv-14 {
	padding: 14px 0;
}
.pdb-14 {
	padding-bottom: 14px;
}
.pd-10 {
	padding: 10px;
}
.pdh-10 {
	padding: 0 10px;
}
.pdv-10 {
	padding: 10px 0;
}
.pdb-10 {
	padding-bottom: 10px;
}
.relative {
	position: relative;
}
.o-hidden {
	overflow: hidden;
}
.anticon {
	outline: none !important;
}
svg {
	outline: none !important;
}
.pointer-all {
	pointer-events: all;
}
