@import '../helpers';

.light-theme body {
	--scrollbar-color: #{$softgray1};
}

body {
	--scrollbar-color: $gray1;
	scrollbar-color: $skyblue $gray1;
	scrollbar-color: $skyblue var(--scrollbar-color);
}

.overflow-auto {
	overflow: auto;
	scrollbar-width: thin;
	scrollbar-color: transparent transparent;
	transition: scrollbar-color 1s cubic-bezier(0.19, 1, 0.22, 1);
}
.overflow-auto:hover,
.overflow-hidden-x:hover {
	transition: scrollbar-color 1s cubic-bezier(0.19, 1, 0.22, 1);
	scrollbar-color: $skyblue transparent;
}

.overflow-hidden-x {
	overflow-x: hidden;
	overflow-y: auto;
	scrollbar-width: thin;
	scrollbar-color: transparent transparent;
}

::-webkit-scrollbar-track,
::-webkit-scrollbar,
::-webkit-scrollbar-corner {
	@include var(background-color, scrollbar-color, $gray2);
}

::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 8px $gray2;
	-webkit-box-shadow: inset 0 0 8px var(--scrollbar-color);
	box-shadow: inset 0 0 8px $gray2;
	box-shadow: inset 0 0 8px var(--scrollbar-color);
}

::-webkit-scrollbar {
	@include box(6px);
}

::-webkit-scrollbar-thumb {
	background-color: $skyblue;
	background-clip: padding-box;
}

/*IE*/
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	.overflow-auto,
	.overflow-hidden-x {
		scrollbar-base-color: transparent;
		scrollbar-face-color: $skyblue;
		scrollbar-3dlight-color: $skyblue;
		scrollbar-highlight-color: $skyblue;
		scrollbar-track-color: #202126;
		scrollbar-arrow-color: #202126;
		scrollbar-shadow-color: $skyblue;
		scrollbar-dark-shadow-color: $skyblue;
		-ms-overflow-style: -ms-autohiding-scrollbar;
	}
}
